@import "../../../../assets/styles/variables.scss";
@import "../../../../assets/styles/mixins.scss";

.card__box {
  display: flex;
  flex-direction: column;
  height: 100%;

  .card__header {
    display: flex;
    background-color: $white;
    border-top-right-radius: 20px;
    background-color: transparent;

    .header-left {
      padding: 38px 40px;
      background-color: $body;
      position: relative;
    }

    .header-left:before {
      @include card_corner();
      @include card_corner_before();
    }

    .header-left:after {
      @include card_corner();
      @include card_corner_after();
    }

    .header-right {
      background-color: $white;
      border-top-right-radius: 20px;
      border-top-left-radius: 20px;
      padding: 26px 0px 0px 40px;

      width: 100%;
    }

    .header-right p {
      @include text_style("Montserrat", 600, 28px, 1.6, $primary);

      text-align: left;
      text-transform: lowercase;
      margin: 0px;
    }
  }

  .card__body {
    padding: 54px 30px 31px 40px;

    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;

    background-color: $white;

    display: flex;
    flex-direction: row;

    background-image: url("../../../../assets/images/card__b.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom 0px;
    height: 100%;

    .card__btn button {
      padding-right: 70px;
    }

    .card__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      text-align: left;
      width: 100%;
      min-width: 200px;
    }

    .card__content p {
      @include text_style("Roboto", 400, 18px, 1.2, $primary);
      margin-top: 0;
    }

    .card__content span {
      @include text_style("Roboto", 400, 15px, 1, $blue);

      padding-bottom: 10px;
      white-space: normal;
    }
  }

  .card__btn-screen {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .card__btn-screen button {
    padding-right: 70px;
    @include reset_cardGradient_btn();
  }

  .card__btn-adptv button {
    display: none;
  }
}

// CardMain
.cardMain__box {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  .cardMain__header {
    display: flex;
    background-color: $white;
    border-top-right-radius: 20px;
    background-color: transparent;

    .headerMain-left {
      padding: 38px 40px;
      background-color: $body;
      position: relative;
    }

    .headerMain-left:before {
      @include card_corner();

      @include card_corner_before();
    }

    .headerMain-left:after {
      @include card_corner();
      @include card_corner_after();
    }

    .headerMain-right {
      background-color: $white;
      border-top-right-radius: 20px;
      border-top-left-radius: 20px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;

      width: 100%;
      padding: 26px 0px 0px 40px;
    }

    .headerMain-right p {
      @include text_style("Montserrat", 600, 28px, 1.6, $primary);
      margin: 0px;
      text-transform: lowercase;
      z-index: 99;
    }

    .headerMain-right div {
      @include text_style("Roboto", 500, 16px, 0.9, $blue);

      display: flex;
      gap: 13px;
      font-variant: small-caps;
      z-index: 99;
    }
  }

  .cardMain__body {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;

    background-color: $white;
    display: flex;
    flex-direction: row;
    padding: 0px 59px 10px 40px;
    height: 100%;

    width: 100%;

    position: relative;

    .block_info {
      display: none;
    }
  }

  .cardMain__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    width: 50%;
    z-index: 80;
  }

  .cardMain__content p {
    @include text_style("Roboto", 500, 16px, 1, $gray);
    text-align: left;
    font-variant: small-caps; // eslint-disable-next-line

    margin-bottom: 39px;
  }

  .gif {
    width: 65%;
    height: 100%;

    position: absolute;
    right: 59px;

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    z-index: 10;

    img {
      width: 37vw;

      height: 31vw;

      object-fit: cover;
      object-position: bottom;
    }
  }
}

.gif-tablet {
  display: none;
}

